.mixin-layout-narrow {
	.popup {
		display: table;
		box-sizing: border-box;
		.popup-content {
			display: table-cell;
			width: auto;
			height: auto;
			top: auto;
			left: auto;
			vertical-align: middle;
			position: relative;
			border-radius: 0;
			margin: 0;
			padding: 0 16px;
			box-shadow: none;
			background: @popup-bg;
			.yes {
				border-right-color: @popup-devider-color-dark;
				box-sizing: border-box;
				margin-bottom: 100px;
				text-align: right;
				padding-right: 50px;
			}
			.no {
				border-left: solid 1px @popup-devider-color-light;
				height: 110px;
				width: 50%;
				box-sizing: border-box;
				margin-bottom: 100px;
				text-align: left;
				padding-left: 50px;
			}
		}
	}

	.header {
		position: absolute;
		background: transparent;
		height: auto;
		.header__in {
			text-align: center;
			left: auto;
			top: auto;
			a.header__logo {
				position: static;
				height: auto;
				width: auto;
				display: inline-block;
				background: transparent;
				margin: 64px 0 32px;
				i {
					position: static;
					width: 115px;
					height: 122px;
					background: url('../img/icons-s51a623a2b8.png') 0 -275px no-repeat;
					display: block;
				}
			}
			ul.header__nav {
				display: block;
				position: relative;
				top: auto;
				left: auto;
				text-align: center;
				margin: 0 @padding-base-horizontal @air * 2;
				li {
					float: none;
					display: inline-block;
					a {
						color: @white;
						margin-right: auto;
						padding-left: 16px;
						padding-right: 16px;
						border-bottom: solid 1px @searchicon-bg;
						border-left: solid 1px @searchicon-bg;
						&.is-active {
							//								border-radius: @radius-mid * 2;
							background: @searchicon-bg;
							color: #333;
						}
					}
				}

			}
			select.header__nav {
				display: none;
			}
		}
		.header__right {
			position: static;
			text-align: center;
			display: inline-block;
			.searchicon {
				position: relative;
				background: url(data:image/png;base64,) no-repeat 4px 4px;
				background-color: @searchicon-bg;
				border-radius: 50%;
				&:after {
					content: '';
					position: absolute;
					top: -4px;
					right: -4px;
					bottom: -4px;
					left: -4px;
					border: solid 1px @searchicon-bg;
					border-radius: 50%;
				}
			}
			.lang {
				margin-left: 0;
				a {
					margin-left: 24px;
					margin-right: 0;
				}
			}
		}
	}

	.superslider {
		margin-top: 0;
		height: auto;
		.superslider__next {
			right: 30px;
			margin-right: 0;
		}
		.superslider__playpause {
			right: 30px;
			margin-right: 0;
		}
		.superslider__pagi {
			right: 30px+20px+15px;
			margin-right: 0;
		}
		.superslider__slide {
			&:after {
				content: '';
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background: @superslirer-overay;
				z-index: -1;
			}
		}
	}

	.page {
		.search.search_wide {
			display: block;
			.search__input {
				padding: 0 @search_wide-width-small;
			}
			.search__filters {
				padding: 0 @search_wide-width-small;
				.search__filters__wrapper {
					height: 220px;
					overflow-y: hidden;
					border-bottom: solid 1px @border-color;
					position: relative;
					z-index: 0;
					.search__filters__wrapped {
						width: 1245px;
						height: auto;
						.search__filter {
							margin-right: @air;
							padding-right: @air;
							&.search__filter1,
							&.search__filter2,
							&.search__filter4,
							&.search__filter5 {
								width: 192px;
							}
							&.search__filter3 {
								width: 397px;
							}
						}
						.catslider {
							.catslider__in {
								.search__categ {
									margin: 0 auto;
									width: 320px;
									.cycle-slide {
										margin-right: 0;
									}
									.cycle-carousel-wrap {
										.cycle-slide {
											width: 80px;
											margin-right: 0;
											position: relative;
											.cat_item {
												text-align: center;
												width: auto !important;
												span {
													position: static !important;
													display: block !important;
													background: none !important;
													margin-left: 0 !important;
													width: auto !important;
													font-size: 14px;
													color: #808080 !important;
													&:before {
														display: none !important;
													}
												}
											}
										}
									}
								}
							}
							.catslider__next {
								right: 0;
							}
						}
					}
				}
				.search__message {
					display: block;
					&:after {
						content: '→';
						display: inline-block;
						margin-left: @air / 2;
					}
				}
			}
		}
	}

	.footer {
		.footer__in {
			.footer__nav, .footer__contacts {
				width: auto;
				left: 0;
				margin: 0 16px;
				padding-right: 2px;
			}
		}
	}
}


